
const UIControls = {
    ShowAddUser: false
}
const UIReducer = (state = UIControls, action) => {

    switch (action.type) {
        
        case 'AddUser':
         //console.log(action.location)
        return {...state,ShowAddUser:!state.ShowAddUser }

        default:
                
        return state
    }
}


export default UIReducer;